<template>
	<div>
	    <mt-tabbar v-model="selected">
         <mt-tab-item id="hit">
           <i class="fa fa-film fa-2x"></i><br>
           热映
         </mt-tab-item>
         <mt-tab-item id="find">
          <i class="fa fa-eye fa-2x"></i><br>
           找片
         </mt-tab-item>
         <mt-tab-item id="me">
            <i class="fa fa-user-o fa-2x"></i><br>
           我的
         </mt-tab-item>
       </mt-tabbar>

	</div>	
</template>

<script>
import Vue from 'vue'
import { Tabbar, TabItem } from 'mint-ui';


Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem);
export default {
  data: function(){
    return {
      selected:'hit'
    }
  },
 
}
</script>
<style scoped>
.fa-2x{
   font-size: 2.3em;
}
.mint-tabbar > .mint-tab-item.is-selected {
    background-color: #eaeaea;
    color: #000;
}
.mint-tabbar > .mint-tab-item {
    color: #969494;
}
</style>


